.app-wrapper {
  min-height: 100vh;
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 0px;
  grid-template-areas:
    "h h"
    "n c";
}
.app-wrapper-content {
    grid-area: c;
    border: 1px dotted black;
    border-top: none;
    border-left: none;
}